document.addEventListener("DOMContentLoaded", function () {
  const containers = document.querySelectorAll(".image-container");
  let currentIndex = 0;

  fadeIn(containers[currentIndex]);

  function fadeIn(container) {
    container.style.display = "flex";
    container.style.opacity = 0;
    setTimeout(() => {
      container.style.opacity = 1;
      setTimeout(() => {
        fadeOut(container);
      }, 3000); // Display each container for 3 seconds
    }, 100); // Start fading in after a short delay
  }

  function fadeOut(container) {
    container.style.opacity = 0;
    setTimeout(() => {
      container.style.display = "none";
      currentIndex = (currentIndex + 1) % containers.length;
      fadeIn(containers[currentIndex]);
    }, 1000); // Fade out takes 1 second
  }
  
  const containers2 = document.querySelectorAll(".image-container2");
  let currentIndex2 = 0;

  fadeIn2(containers2[currentIndex2]);

  function fadeIn2(container) {
    container.style.display = "flex";
    container.style.opacity = 0;
    setTimeout(() => {
      container.style.opacity = 1;
      setTimeout(() => {
        fadeOut2(container);
      }, 3000); // Display each container for 3 seconds
    }, 100); // Start fading in after a short delay
  }

  function fadeOut2(container) {
    container.style.opacity = 0;
    setTimeout(() => {
      container.style.display = "none";
      currentIndex2 = (currentIndex2 + 1) % containers2.length;
      fadeIn2(containers2[currentIndex2]);
    }, 1000); // Fade out takes 1 second
  }
});
